<template>
    <div class="setPassword">
      <header-component title="设置密码"></header-component>
      <router-view></router-view>
      <div class="am-top">
        <label class="mima">设置新密码</label>
        <div class="am-passWord">
          <input type="password" class="am-pwd" placeholder="填写密码" maxlength="16">
          <img class="am-password-cancel" src="../../assets/images/Login/Login_05.png"/>
          <img class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
        </div>
        <p class="am-loginBtn">
          <button type="button" class="am-btn-default">完成</button>
        </p>
      </div>

    </div>
</template>

<script>
  import HeaderComponent from './header.vue'
  var urlhost = process.env.API_AJAX;
    export default {
      name:"setpassword",
        data() {
            return {
            }
        },
        components: {
            HeaderComponent,
        }
    }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .setPassword{
    .header{
      position: fixed;
      top:0;
      left:0;
      width: 100%;
      height: n(90);
      display: inline-flex;
      align-items: center;
      border-bottom: 1px solid #d8d8d8;
      img{
        position: absolute;
        left: 0.2rem;
        width: n(70);
        height: n(50);
      }
      a{
        width: 100%;
        display: inline-block;
        text-align: center;
        font-size: n(34);
      }
    }
    .am-top{
      width: 90%;
      margin: auto;
      margin-top: n(120);
      .mima{
        margin-top: n(20);
        font-weight: bold;
        display: inline-block;
        font-size: n(24);
        color: #424242;
      }
      .am-passWord{
        width: 100%;
        height: n(105);
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #f3f3f3;
        input{
          border: none;
          outline: none;
          width: 70%;
          height: n(70);
          font-size: n(30);
          color: #b0b0b0;
        }
        img{
          width: n(50);
          height: n(50);
        }
      }
      .am-loginBtn{
        width: 100%;
        margin: n(80) 0 0 0;
        button{
          width: 100%;
          height: n(80);
          background: #fee837;
          font-size: n(30);
          border: none;
        }
      }
    }
  }





</style>



